body, html {
  font-size: 20px;
  margin: 0 auto;
  font-family: "Lato", sans-serif;
  font-weight: 400;
  line-height: 1.4; }
  @media screen and (max-width: 1024px) {
    body, html {
      font-size: 18px; } }

a {
  text-decoration: none; }
  a:hover {
    opacity: 0.5; }

/* This file is to override Storybook default styles */
body {
  /* Needed to get Storybook Backgrounds Addon to work */
  background-color: transparent !important;
  height: auto !important;
  /* Add padding to story page to give it some space against the edges */
  padding: 20px !important; }
